<template>
  <div class="sitemap">
      <h1>{{ name }}</h1>
      <b-img src='../assets/logo.jpg' fluid/>
      <div class='w-100'></div>
      <div class='mt-3 mb-3'>
          <b-button :to="{name:'InsertResume'}">InsertResume</b-button>
          <b-button :to="{name:'ReadResume'}">ReadResume</b-button>
      </div>
      <b-button @click="axiosTest">AXIOSTEST</b-button>
  </div>
</template>

<script>
export default {
  name: 'SiteMap',
  data () {
    return {
      name: 'KenBa Kitchen'
    }
  },
  methods: {
    axiosTest (evt) {
      this.axios.post('http://localhost:8080/recipe', { name: 'Apple', count: 10 })
        .then(function (result) {
          console.log(result.data)
        }).catch((e) => { console.log(e) })
      this.axios.put('http://localhost:8080/recipe/4', { name: 'App', count: 8 })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
</style>



